@import "variables.css";

.footer {
    margin-top: auto;
    background-color: var(--green) !important;
    bottom: 0;
    padding: 18px;
    color: white;
    width: 100%;
    height: 7%;
    box-sizing: border-box;
    display: flex;
    gap: 1em;
    justify-content: center;
    align-items: center;
}

.icon {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30px;
}