@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Color Variables */
:root {
    --dk-green: #5b643f;
    --green: #BBC893;
    --lt-green: #E0E6B3;
    --lt-tan: #FEFAE0;
    --tan: #FAEDCD;
    --dk-tan: #D4A373;
    --brown: #755E2E;
    --black: #1F140A;
    --link-blue: #92a8d1;
    --font-family: 'Quicksand', sans-serif;
}
html {
    height: 100%;
}

body {
    background-color: var(--green) !important;
    color: var(--dk-green);
    font-family: var(--font-family);
    height: 100%;
    font-size: 16px;
    display: flex;
    flex-direction: column;
}

#root {
    height: 100%;
}

main {
    height: 100%;
    display: flex;
    flex-direction: column;
}

hr {
    margin: 0;
}

.container {
    height: 100%;
    background-color: var(--lt-tan);
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    padding: 1em;
}



.link {
    color: var(--link-blue);
}

*/ h1 {
    color: var(--black);
}

h4 {
    margin-bottom: 0;
}

.pageTitle {
    padding-bottom: 10px;
    text-align: center;
}

div>hr {
    background-color: var(--dk-green);
}
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    width: 100%;
    z-index: 100;
    height: 7%;
    background-color: var(--green);
    padding: 0.25em;
    box-sizing: border-box;
    color: var(--black);
}

.navbar a {
    text-decoration: none;
    color: var(--black);
}

.navbar .nav-links {
    display: flex;
    grid-gap: 1em;
    gap: 1em;
}

.navbar .nav-links a.active {
    font-weight: bolder;
}
.footer {
    margin-top: auto;
    background-color: var(--green) !important;
    bottom: 0;
    padding: 18px;
    color: white;
    width: 100%;
    height: 7%;
    box-sizing: border-box;
    display: flex;
    grid-gap: 1em;
    gap: 1em;
    justify-content: center;
    align-items: center;
}

.icon {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30px;
}
.skills {
    display: flex;
    flex-direction: column;
    grid-gap: 0.5em;
    gap: 0.5em;
    box-sizing: border-box;
}

.skills .skill {
    border: 1px solid var(--dk-green);
    margin: .25em;
    border-radius: 0.5em;
    position: relative;
    background: var(--lt-tan);
    box-shadow: 2px 2px 7px -2px var(--dk-green);
    font-size: 1.25em;
    display: flex;
}

.bar {
    padding: .5em;
    background-color: var(--dk-green);
    border-radius: .5em;
    color: var(--lt-tan);
    font-weight: bold;
    height: .25em;
    line-height: 0em;
}

.skillDetails {
    display: inline-block;
}

.skillLevel {
    float: right;
}

.img {
    float: left;
    height: 250px;
    margin: 0 20px 0 0;
    border-radius: 6em;
}
.portfolio-img {
    margin-bottom: 20px;
    opacity: 100%;
}

.card-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    grid-gap: 2em;
    gap: 2em;
    margin: 2em;
}

.card-container .card {
    box-shadow: 2px 2px 7px -2px var(--black);
    border: transparent;
    border-radius: .25em;
    display: flex;
    flex-direction: column;
    width: 25%;
}

.card-container .card img {
    height: 218px;
}

.card-container .card .card-body {
    background-color: var(--dk-green);
    color: var(--lt-tan);
    border-radius: 0 0 0.25em 0.25em;
    padding: 1em;
    display: flex;
    flex-direction: column;
}

.card-container .card .card-body .card-title {
    font-size: 1.5em;
    margin-top: 1em;
}

.card-container .card .card-body .buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    text-align: center;
    grid-gap: 1em;
    gap: 1em;
}

.card-container .card .card-body .buttons .btn {
    border-radius: 2em;
    width: 200px;
    padding: .5em;
}

.card-container .card .card-body .buttons .btn-primary {
    background-color: white !important;
    border-color: white !important;
    box-shadow: 2px 2px 7px -2px var(--brown);
    color: var(--black);
    text-decoration: none;
}

.card-container .card .card-body .buttons .btn-secondary {
    background-color: var(--dk-green);
    border-color: var(--dk-green);
    box-shadow: 2px 2px 7px -2px var(--brown);
    color: var(--lt-tan);
    padding: .25em 1em;
    font-size: 1.25em;
}

@media only screen and (max-width: 600px) {
    .card-container {
        flex-direction: column;
    }

    .card-container .card {
        width: 100%
    }

    .card-container .card .card-body .buttons .btn {
        width: 100px;
    }
}
.job-container {
    display: grid;
    box-sizing: border-box;
    margin: 2em;
}

.job-container .job-header {
    display: flex;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
}

.job-container .job-header .image-role-dates {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.job-container .job-header .image-role-dates .role-dates {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    grid-gap: 1rem;
    gap: 1rem;
    height: 100%;
    width: 100%;
}

.job-container .job-header .image-role-dates .role-dates.role,
.job-container .job-header .image-role-dates .role-dates .dates {
    width: 100%;
    text-align: left;
    white-space: nowrap;
}

.company,
.location {
    text-align: right;
}

.company-location {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
}

.chip-container {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem
}

.chip {
    flex: 1 1;
    padding: 12px 24px;
    border-radius: 6px;
    white-space: nowrap;
    text-align: center;
    background-color: var(--dk-green);
    color: var(--lt-tan);
    font-weight: bold;
    border-radius: 1em;
    width: auto;
}

@media only screen and (max-width: 600px) {
    .job-container {
        margin: 0;
    }
}
